.main-body-item {
  height: 100%;
  width: 80vw;
  max-width: 1000px;
  padding: 30px 0 30px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
  color: white;
}

.footer {
  padding-bottom: 20px;
  background-color: rgba(52, 58, 64, 0.5);
  /* background-image: url('./footer-background.png')!important; */
  background-size: cover;
  background-position: center;
  overflow: hidden;
  position: relative;
}

.demo-bg {
  opacity: 0.4;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: auto;
}

.demo-content {
  position: relative;
}

.container {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 0;
  overflow: hidden;
  box-shadow: 0px 10px 24px 0px rgba(50, 50, 75, 0.49);
  font-size: 0;
  background-color: yellow;
  margin: auto;
  z-index: -1000;
  padding-bottom: 55%;
}

